.gpt3__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 1.8rem;
    margin: 3.6rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt3__cta-content p {
    font-family: var(--font-family);
    font-weight: 510;
    font-size: 14px;
    line-height: 31px;
    color: black;
    margin-bottom: 0.6rem;
}

.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 22px;
    line-height: 46px;
    color: #000000;
}
.gpt3__cta-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-button button {
    background: #000000;
    color: #ffffff;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 19px;
    line-height: 31px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .gpt3__cta {
    flex-direction: column;
    }
    .gpt3__cta-button {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }

    .gpt3__cta-button {
        font-size: 18px;
        line-height: 30px;
    }

    .gpt3__cta-button button {
        font-size: 14px;
        line-height: 28px;
    }
}