.gpt3__possibility {
    display: flex;
    flex-direction: row;

}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 1.9rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 490;
    font-size: 20px;
    line-height: 30px;
    color: #71e5ff;
    margin-bottom: 1rem;
}

.gpt3__possibility-content h4:last-child {
    color: #ff8a71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 35px;
    line-height: 46px;
}
.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-size: 17px;
    line-height: 31px;
    color: var(--color-text);
    margin-bottom: 2rem;
    margin-top: 2rem;
}

@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image {
        margin: 1rem 0;
    }

    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}